$(document).ready(function() {

    // Menu
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header__wrap').toggleClass('header__wrap--active')
        $("body").toggleClass("fixed-body")
    })

    // Timer
    var Update = function() {
        $('.sec').each(function() {
            var count = parseInt($(this).html());
            if (count !== 0) {
                $(this).html(count - 1);
                if ($('.sec').html().length == 1) {
                    $('.sec').addClass("add-zero")
                } else {
                    $('.sec').removeClass("add-zero")
                }
            } else {
                dec_min();
            }
        })
    }
    setInterval(Update, 1000);

    function dec_min() {
        min = parseInt($('.min').html());
        if (min !== 0) {
            $('.min').html(min - 1);
            $('.sec').html(59);
            $('.sec').removeClass("add-zero")
            if ($('.min').html().length == 1) {
                $('.min').addClass("add-zero")
            } else {
                $('.min').removeClass("add-zero")
            }
        } else {
            dec_hr();
        }
    }

    function dec_hr() {
        hr = parseInt($('.hr').html());
        if (hr !== 0) {
            $('.hr').html(hr - 1);
            $('.min').html(59);
            $('.sec').html(59);
            $('.sec').removeClass("add-zero")
            $('.min').removeClass("add-zero")
            if ($('.hr').html().length == 1) {
                $('.hr').addClass("add-zero")
            } else {
                $('.hr').removeClass("add-zero")
            }
        } else {
            dec_day();
        }
    }

    function dec_day() {
        day = parseInt($('.day').html());
        if (day !== 0) {
            $('.day').html(day - 1);
            $('.hr').html(23);
            $('.min').html(59);
            $('.sec').html(59);
            $('.sec').removeClass("add-zero")
            $('.min').removeClass("add-zero")
            if ($('.day').html().length == 1) {
                $('.day').addClass("add-zero")
            } else {
                $('.day').removeClass("add-zero")
            }
        } else {
            pass;
        }
    }

})